@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
*{
    margin:0%;
    padding: 0%;
    font-family: "Roboto",sans-serif;
    color:white ;

    &::-webkit-scrollbar{
        display: none;
    }
}
a{
    color: white;
    text-decoration: none;
    
}


.header{
    height:100px;
    background-color:black;
    display:flex;
    align-items: center;
    
    box-sizing: border-box;
    justify-content: space-between;
    >img{
        height: 100%;
    }
    >div{
        width: 100%;
        margin-left: 4vmax;
        >a{
            margin: 2vmax;
            font-size: 1.3rem;
            font-weight: 300;
        }
    }
    >svg{
        color: white;
        font-size: 1.5rem;
        margin-right: 3rem;
        cursor: pointer;
        &:hover{
            color: rgb(87, 82, 82);
        }
    }
    
}



